p {line-height: 1.8;}
.u-center-text {text-align: center}
.t-capitalize{text-transform: capitalize!important;}
.t-uppercase{text-transform: uppercase!important;}
.flex-box {display: flex;}
.justify-content {
    &--center {
        justify-content: center;
    }
    &--space-between {
        justify-content: space-between;
    }
}
.align-items {
    &--center {
        align-items: center;
    }
}


